// Импорт useState - для создания локального состояния routeData
// Импорт useEffect - для выполнения эффекта только один раз
import React, { useState, useEffect } from 'react';
import {fetchJson} from "./responseJson";

// Функция для добавления компонента FrameTime
function FrameTime() {
    // Объект для хранения данных из JSON файла
    const [routeData, setRouteData] = useState({
        route_PK: '',
        to: '',
        from: '',
        number_of_transport: '',
        days: '',
        type_of_transport: ''
    });

    // "Одноразовое" выполнение функции при создании компонента
    useEffect(() => {
        // Вызываем функцию с запросом к API
        fetchJson(setRouteData);

    }, []);

    // Получаем значения из файла и возвращаем 'Ежедневно' или 'По будням'
    const dateStr = (routeData.days === 'пвсчп св') ? 'Ежедневно' : 'По будням';

    return (
        <div className="frame">
            <p className="text_type">Дни курсирования</p>
            <div className="type_of_time">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24"><path d="M9 1V3H15V1H17V3H21C21.5523 3 22 3.44772 22 4V20C22 20.5523 21.5523 21 21 21H3C2.44772 21 2 20.5523 2 20V4C2 3.44772 2.44772 3 3 3H7V1H9ZM20 11H4V19H20V11ZM7 5H4V9H20V5H17V7H15V5H9V7H7V5Z"></path></svg>
                <p className="text_content">{dateStr}</p>
            </div>
        </div>
    );
}

export default FrameTime;